/* global Office */

// Global variable for email data
let emailData = {};
let item;

// Initialize Office add-in
Office.onReady(() => {
  item = Office.context.mailbox.item;
});

/**
 * Get email data and prepare default values for a new email.
 * @param {Office.AddinCommands.Event} event
 */

const baseURL = "http://www.centreblock.org/";

function GetDefault(event) {
  // Extract email fields
  const receiver = item.to ? item.to.map(recipient => recipient.emailAddress).join(", ") : "";
  const sender = item.from ? item.from.emailAddress : "";
  const subject = item.subject || "blank";
  const ccList = item.cc ? item.cc.map(cc => cc.emailAddress) : [];

  // Format email timestamp
  const emailTimestamp = formatEmailTimestamp(item.dateTimeCreated);

  // Construct emailData object
  emailData = {
    htmlBody: "",
    campaign: receiver ? [receiver] : [],
    source: sender,
    medium: emailTimestamp,
    content: subject,
    term: ccList,
  };

  // Generate UTM URL and add it to email body
  const UTM_URL = generateURLFromEmailData(emailData);
  emailData.htmlBody = UTM_URL;

  // Display a new email form with the prepared data
  Office.context.mailbox.displayNewMessageForm({
    toRecipients: emailData.campaign,
    ccRecipients: ccList.map(email => ({ emailAddress: email })),
    subject: subject,
    // htmlBody: `<a href="${UTM_URL}" target="_blank">${baseURL}</a>`,
  });

  event.completed();
}

/**
 * Format email timestamp into a specific format.
 * @param {string} emailTimestamp
 * @returns {string} Formatted timestamp
 */
function formatEmailTimestamp(emailTimestamp) {
  const timestamp = new Date(emailTimestamp);
  const day = String(timestamp.getDate()).padStart(2, "0");
  const month = String(timestamp.getMonth() + 1).padStart(2, "0");
  const year = String(timestamp.getFullYear()).slice(-2);
  const hours = String(timestamp.getHours()).padStart(2, "0");
  const minutes = String(timestamp.getMinutes()).padStart(2, "0");

  return `${day}-${month}-${year}_${hours}.${minutes}`;
}

/**
 * Generate a UTM URL using email data.
 * @param {Object} emailData
 * @returns {string} Generated UTM URL
 */
function generateURLFromEmailData(emailData) {
  // Construct UTM parameters from emailData
  const utmParams = {
    utm_campaign: emailData.campaign + "+to" || "default_campaign",
    utm_source: emailData.source + "+from" || "default_source",
    utm_medium: emailData.medium || "default_medium",
    utm_content: emailData.content || "default_content",
    utm_term: emailData.term || "default_term",
  };

  const url = new URL(baseURL);
  Object.keys(utmParams).forEach(key => {
    url.searchParams.append(key, utmParams[key]);
  });

  return decodeURIComponent(url.toString());
}

// Register the functions with Office actions
Office.actions.associate("GetDefault", GetDefault);


// Helper function to format the current date and time
// Helper function to format the current date and time
function getCurrentDateTime() {
  const now = new Date();
  const year = now.getFullYear().toString().slice(-2); // Last two digits of the year
  const month = (now.getMonth() + 1).toString().padStart(2, "0");
  const day = now.getDate().toString().padStart(2, "0");
  const hours = now.getHours().toString().padStart(2, "0");
  const minutes = now.getMinutes().toString().padStart(2, "0");
  return `${day}-${month}-${year}_${hours}.${minutes}`;
}
// Perform functionality when a new message is composed
async function SetDefaultData(event) {
  try {
    // Get the "To" recipients
    const toRecipients = await new Promise((resolve, reject) => {
      item.to.getAsync((result) => {
        if (result.status === Office.AsyncResultStatus.Succeeded) {
          resolve(result.value);
        } else {
          reject(result.error);
        }
      });
    });

    // Get the "CC" recipients
    const ccRecipients = await new Promise((resolve, reject) => {
      item.cc.getAsync((result) => {
        if (result.status === Office.AsyncResultStatus.Succeeded) {
          resolve(result.value);
        } else {
          reject(result.error);
        }
      });
    });

    // Get the email subject
    const subject = await new Promise((resolve, reject) => {
      item.subject.getAsync((result) => {
        if (result.status === Office.AsyncResultStatus.Succeeded) {
          resolve(result.value);
        } else {
          reject(result.error);
        }
      });
    });

    // Extract recipient addresses
    const toAddresses = toRecipients.map((recipient) => recipient.emailAddress).join(", ");
    const ccAddresses = ccRecipients.length > 0
      ? ccRecipients.map((recipient) => recipient.emailAddress).join(", ")
      : "blank";

    const medium = (`e-mail_${getCurrentDateTime()}`).replace(/ /g, "_");

    let utmCampaign = toAddresses.replace(/ /g, "_");
    let utmSource = Office.context.mailbox.userProfile.emailAddress.replace(/ /g, "_");
    let utmMedium = medium.replace(/ /g, "_");
    let utmContent = subject.replace(/ /g, "_");
    let utmTerm = ccAddresses.replace(/ /g, "_");

    const queryString = `?utm_campaign=${utmCampaign}&utm_source=${utmSource}&utm_medium=${utmMedium}&utm_content=${utmContent}&utm_term=${utmTerm}`;

    // Combine the base URL with the query string
    const finalUrl = baseURL + queryString;
    console.log(finalUrl);
    let signature;
    let userName = Office.context.mailbox.userProfile.displayName;
    // Get the signature HTML from localStorage
    let signatureHtmlObj = localStorage.getItem("signatureHtml");
    if (!signatureHtmlObj) {
      console.error("No signature HTML found in localStorage.");
      signature = `<p>${userName}</p><p><a data-fr-linked=\"true\" href=\"http://www.centreblock.org/\" id=\"isPasted\">http://www.centreblock.org/</a></p><p><img src=\"data:image/png;base64,\" style=\"width: 114px;\" class=\"fr-fic fr-dib fr-fil\"></p><p><br></p>`;
    } else {
      let signaturehtmlobgparse = JSON.parse(signatureHtmlObj);
      signature = signaturehtmlobgparse.html;
    }

    const linkHTML = `<a href="${finalUrl}" target="_blank">${baseURL}</a>`;



    const cleanedSignature = signature.replace(
      /<p[^>]*data-f-id="pbf"[^>]*>.*?<a[^>]*href="https:\/\/www\.froala\.com\/wysiwyg-editor\?pb=1"[^>]*>.*?<\/a><\/p>/g,
      ""
    );
    // Replace only the href in the a tag with finalUrl
    const updatedSignature = cleanedSignature.replace(/<a[^>]*>.*?<\/a>/g, linkHTML);

    console.log("Updated Signature HTML:", updatedSignature);

    // Use setSignatureAsync to update the email signature
    Office.context.mailbox.item.body.setSignatureAsync(
      updatedSignature,
      { coercionType: "html" },
      function (asyncResult) {
        if (asyncResult.status === Office.AsyncResultStatus.Succeeded) {
          console.log("setSignatureAsync succeeded");
          event.completed();
        } else {
          console.error(asyncResult.error);
          event.completed();
        }
      }
    );

  } catch (error) {
    console.error("Error populating UTM defaults:", error);
    event.completed();
  }
  event.completed();
}
// Register the functions with Office actions.
Office.actions.associate("SetDefaultData", SetDefaultData);




